<template>
    <div class="container">
      <h1>采购单明细汇总</h1>
      <h2>采购单ID：{{purchases.purchase_id}}</h2>
      <el-button type="primary" class="addItem" @click="addPurchasesDetail()">新增采购单明细</el-button>
      <div class="tableContainer">
        <el-table
      height="250"
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="ID"
        width="50">
      </el-table-column>
      <el-table-column
        prop="ref"
        label="采购单号"
        width="150">
      </el-table-column>
      <el-table-column
        prop="image"
        label="产品图片"
        width="120">
          <template slot-scope="scope">
              <div v-html="scope.row.image"></div>
          </template>
      </el-table-column>
      <el-table-column
        prop="sku"
        label="SKU"
        width="100">
      </el-table-column>
      <el-table-column
        prop="color"
        label="颜色"
        width="50">
      </el-table-column>
      <el-table-column
        prop="size"
        label="尺寸"
        width="50">
      </el-table-column>
      <el-table-column
        prop="link"
        label="采购链接"
        width="100">
      </el-table-column>
      <el-table-column
        prop="price"
        label="价格"
        width="50">
      </el-table-column>
      <el-table-column
        prop="count"
        label="数量"
        width="50">
      </el-table-column>
      <el-table-column
        prop="supplier_platform"
        label="平台"
        width="70">
      </el-table-column>
      <el-table-column
        prop="supplier_ref"
        label="平台单号"
        width="150">
      </el-table-column>
      <el-table-column
        prop="supplier_name"
        label="供应商名称"
        width="100">
      </el-table-column>
      <el-table-column
        prop="logistics"
        label="物流单号"
        width="100">
      </el-table-column>
      <el-table-column
        prop="create_time"
        label="采购单生成时间"
        width="100">
      </el-table-column>
      <el-table-column
        prop="action_time"
        label="采购时间"
        width="100">
      </el-table-column>
      <el-table-column
        prop="username"
        label="采购人"
        width="70">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态"
        width="70">
      </el-table-column>
      <el-table-column
        prop="memo"
        label="备注">
      </el-table-column>

      <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.row)" type="text" size="small">编辑</el-button>
              <!--<el-button @click="handleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button>-->
            </template>
      </el-table-column>


      </el-table>
        
      </div>
    </div>
</template>

<script>
    
  export default {
    name: "purchases-detail",
    data() {
        return {
          purchases: {
            purchase_id: '',
            // purchase_ref: '',
          },
          tableData: [/*{
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },
          {
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },*/ 
          ],
          totalSize: 0,
          currentPage: 1,
          pageSize: 20
        }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      addPurchasesDetail(row) {
        this.$router.push({ path: '/purchases-detail/add',query: row})
      },
      handleEdit(row) {
        this.$router.push({ path: '/purchases-detail/update',query: row})
      },
      // handleDelete(row,index) {
      //   let url = 'http://api.admin.com/purchases/'+row.id;
      //   let that = this;
      //   this.$confirm('是否确认删除该条记录?', '提示', {
      //     confirmButtonText: '确定',
      //     cancelButtonText: '取消',
      //     type: 'warning'
      //   }).then(() => {
      //     that.$axios.delete(url).then((res) => {
      //       console.log(res);
      //       if(res.status == 200 || res.status == 204){
      //         that.tableData.splice(index,1);
      //         that.$message({
      //           type: 'success',
      //           message: '删除成功!'
      //         });
      //       }else {
      //         this.$message({
      //           type: 'error',
      //           message: '删除失败!'
      //         });
      //       }
      //     }).catch(() => {
      //       console.log(res);
      //       this.$message({
      //         type: 'error',
      //         message: '删除失败!'
      //       });
      //     })
      //   }).catch(() => {});
      // },
      initTable() {
        let initList = [];
        let url = "http://api.orkowms.me/purchases-detail?page="+this.currentPage+"&per-page="+this.pageSize;
        let config = {
            headers : {
                'Content-Type':'application/json;charset=UTF-8'
            },
        };
        let that = this;
        if(that.purchases.purchase_id){
            config = {
                headers : {
                     // 'Content-Type':'application/x-www-form-urlencoded'
                },
            };
            url = "http://api.orkowms.me/purchases-detail?WmsPurchasesDetailSearch[purchase_id]="+this.purchases.purchase_id;
        }
          // document.write(this.purchases.purchase_id);exit;
        this.$axios.get(url,config).then((res) => { 
          if(res.data.data){
            let tasksList = res.data.data;
            that.totalSize = parseInt(res.data._meta.totalCount);
            //对象转数组
            var arr = [];
            for (var i in tasksList) {
                arr.push(tasksList[i]);
            }

            arr.forEach((task) => {
              let tempTask = {};
              tempTask.id = task.id;
              tempTask.ref = task.ref;
              tempTask.image = "<img width='100' src='"+task.image+"'>";
              tempTask.sku = task.sku;
              tempTask.color = task.color;
              tempTask.size = task.size;
              tempTask.link = task.link;
              tempTask.price = task.price;
              tempTask.count = task.count;
              tempTask.supplier_platform = task.supplier_platform;
              tempTask.supplier_ref = task.supplier_ref;
              tempTask.supplier_name = task.supplier_name;
              tempTask.logistics = task.logistics;
              tempTask.create_time = task.create_time;
              tempTask.action_time = task.action_time;
              tempTask.username = task.username;
              tempTask.status = task.status;
              tempTask.memo = task.memo;
              initList.push(tempTask);
            })
          }
          that.tableData = initList;
          console.log(res)
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    created() {
        let row = this.$route.query;
        this.purchases.purchase_id = row.id;
        // this.purchases.purchase_ref = row.ref;
        this.initTable();
    }
  }
</script>

<style lang="stylus">
  .container
    padding 30px
    position relative
  .addItem
      margin 10px 0
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 148px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



